import React from 'react';
import { useEffect } from 'react';
import Heading from '../common/Heading';


function WeddDetails(props) {
    const setCountDown = () => {
        const countDate = new Date('Dec 24, 2022, 18:00:00').getTime()
        const interval = setInterval(function() {
            const now = new Date().getTime()
            const distance = countDate - now
    
            // calculate days, hours, minutes, seconds
            const days = Math.floor(distance / (1000 * 60 * 60 * 24))
            const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
            const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
            const seconds = Math.floor((distance % (1000 * 60)) / 1000)
            
            document.querySelector('.countdown').innerHTML = days + "d " + hours + "h "
            + minutes + "m " + seconds + "s"
    
            if (distance < 0) {
                clearInterval(interval)
                document.querySelector('.countdown').innerHTML = 'Quy thi anh em!'
            }
        }, 1000)
    }

    useEffect(() => {
        setCountDown()
    }, [])

    return (
        <>
            <section className="weddetails card-section">
                <div className="overlay-bg"></div>
                <Heading title='m Ci' desc=''/>
                <p className='message'>Tic ci s din ra sm hn thng thng mt cht - vo lc 6 gi ti. Mi ngi chu kh n ng gi nh!</p>
                <div className="wed-wrapper">
                    <div className="wed-info">
                        <div className="left">
                            <h2>Lc</h2>
                            <p>24 thng 12 2022, <br /> 6:00 PM</p>
                        </div>
                        <div className="img">
                            <img src="../images/hero/flower.webp" alt="" />
                        </div>
                        <div className="right">
                            <h2>Ti</h2>
                            <p>Pergola<br /> 
                            28A Trn Cao Vn, Phng 12, <br />
                            Qun Ph Nhun, Thnh ph H Ch Minh</p>
                        </div>
                    </div>
                </div>
                <div className="countdown">
                </div>
            </section>
        </>
    );
}

export default WeddDetails;